import React, { useEffect, useState } from "react";
import { getPrice, removeProps } from "../../../utils/oxMethods";
import { Link } from "react-router-dom";
import { quantityInputs } from "../../../utils";
import { getVariation } from "../../../api/products";
import MediaImage from "../../../components/features/product/MediaImage";
import { useSelector } from "react-redux";

function SellRecordTwo({ refundRecord, sellRecord }) {
	const [product, setProduct] = useState(null);
	let mount = true;
	const { settings } = useSelector(state => state.marketData);
	const [properties, setProperties] = useState();
	useEffect(() => {
		getVariation(sellRecord.variation).then(res => {
			if (mount) {
				setProduct(res.data.variation);
			}
		});
		return () => {
			mount = false;
		};
	}, []);

	useEffect(() => {
		if (product) {
			setProperties(
				removeProps(product.properties, settings.variationProperty)
			);
		}
	}, [product?.properties]);

	useEffect(() => {
		quantityInputs();
	});

	if (refundRecord) {
		return (
			<div className="cart-item p-0 flex-column align-items-start">
				<div className="row m-0 cart-product-card w-100">
					<div className="col-3 p-0">
						<div className="product-media-wrapper">
							<span className="count_order">
								{refundRecord.count}
							</span>
							<Link
								className="product-media"
								to={`${process.env.PUBLIC_URL}/product/${refundRecord.variation}`}
							>
								<MediaImage
									image={product?.images[0]}
									largeSize="300x_"
									mediumSize="300x_"
									smallSize="150x_"
									alt="Product"
								/>
							</Link>
						</div>
					</div>
					<div className="col-9 d-flex cart-product-info m-0">
						<div className="cart-item-infoblock w-100">
							<div>
								<Link
									className="text-dark"
									to={`${process.env.PUBLIC_URL}/product/${refundRecord.variation}`}
								>
									{product?.name}
								</Link>
								<div className="d-flex">
									{product ? (
										properties?.map((prop, index) => {
											return (
												<div
													key={index}
													className="cart-variation-pr"
												>
													<span>{prop.name}</span>
													<span>{prop.value}</span>
												</div>
											);
										})
									) : (
										<div className="short-line-shimmer"></div>
									)}
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}

	let image = product?.images[0];

	return (
		<div className="cart-item p-0 flex-column align-items-start">
			<div className="row m-0 cart-product-card w-100">
				<div className="col-3 p-0">
					<div className="product-media-wrapper">
						<span className="count_order">{sellRecord.count}</span>
						<Link
							className="product-media"
							to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
						>
							{image ? (
								<MediaImage
									image={image}
									largeSize="300x_"
									mediumSize="300x_"
									smallSize="150x_"
									alt="Product"
								/>
							) : null}
						</Link>
					</div>
				</div>
				<div className="col-9 d-flex cart-product-info m-0">
					<div className="cart-item-infoblock w-100">
						<div>
							<Link
								className="text-dark"
								to={`${process.env.PUBLIC_URL}/product/${sellRecord.variation}`}
							>
								{sellRecord.variationName}
							</Link>
							<div className="d-flex">
								{product ? (
									properties?.map((prop, index) => {
										return (
											<div
												key={index}
												className="cart-variation-pr"
											>
												<span>{prop.name}</span>
												<span>{prop.value}</span>
											</div>
										);
									})
								) : (
									<div className="short-line-shimmer"></div>
								)}
							</div>
						</div>
						<div className="cart-amount-product">
							{sellRecord?.discount[sellRecord.discount.first] >
							0 ? (
								<div className="search-result-price">
									<span className="new-price">
										{getPrice(sellRecord.finalPrice, true)}
									</span>
									<span className="old-price text-through">
										{getPrice(sellRecord.price, true)}
									</span>
								</div>
							) : (
								getPrice(sellRecord.total, true)
							)}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}

export default SellRecordTwo;
