import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import car from "../../assets/images/Vector.png";
import { getDeliveryGroups } from "../../api/user";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import { useSelector } from "react-redux";
import { calculateSellsDiscount } from "../checkout/helpers";
import { formatterCurrency } from "../../utils/string";
import SellRecordTwo from "../profile/dashboard/SellRecordTwo";
import { getPrice } from "../../utils/oxMethods";

export default function OrderDetails(props) {
	const { t } = useTranslation();
	const { token, id } = props.match.params;
	const { paymentMethods } = useSelector(state => state.marketData.cashdesk);
	const { shippingServices } = useSelector(state => state.marketData);

	const [loading, setLoading] = useState(true);
	const [deliveryGroup, setDeliveryGroup] = useState();
	const [paidPaymentMethod, setPaidPaymentMethod] = useState();
	const [shippingService, setShippingService] = useState();
	const [total, setTotal] = useState(0);
	const [sellsDiscount, setSellsDiscount] = useState(0);

	function getDeliveryGroup() {
		getDeliveryGroups(token).then(res => {
			if (res) {
				const dg = res.items.find(sell => sell.id === parseInt(id));
				console.log(dg);
				setDeliveryGroup(dg);
				setShippingService(
					shippingServices.find(
						s => s.id === dg.sells[0].shippingService
					)
				);
				setTotal(
					dg.sells.reduce(
						(sum, sell) => sum + getPrice(sell.subtotal),
						0
					)
				);
				setSellsDiscount(calculateSellsDiscount(dg.sells));
				setPaidPaymentMethod(
					paymentMethods.find(
						p => p.id === dg.sells[0].payments[0].paymentMethod
					)
				);
				setLoading(false);
			}
		});
	}

	useEffect(() => {
		const header = document.querySelector("header");
		const footer = document.querySelector("footer");
		header.classList.add("d-none");
		footer.classList.add("d-none");

		getDeliveryGroup();
		return () => {
			const header = document.querySelector("header");
			const footer = document.querySelector(".footer.footer-four");
			if (header) header.classList.remove("d-none");
			if (footer) footer.classList.remove("d-none");
		};
	}, []);

	if (loading) {
		return <LoaderOverlay />;
	}

	return (
		<div id="order-details" className="order-details-page">
			<div className="orders_header">
				<h3>{t("order-details")}</h3>
			</div>
			<div className="order-content">
				<div className="info-order-item">
					<div className="info_order">
						<div className="info-order-content">
							<div className="delivery_type">
								<img
									src={car}
									alt="car"
									width="20"
									height="20"
								/>
								<p className="delivery_name">
									{shippingService.name}
								</p>
								{shippingService.price ? (
									shippingService.price[
										shippingService.price.first
									] > 0 ? (
										<p className="delivery_price">
											{formatterCurrency(
												shippingService.price[
													shippingService.price.first
												]
											)}
										</p>
									) : (
										<p className="delivery_price">
											{formatterCurrency(
												shippingService.price[
													shippingService.price.first
												]
											)}
										</p>
									)
								) : (
									""
								)}
							</div>
							{deliveryGroup.sells
								.filter(s => !s.deleted)
								.map((sell, index) => {
									return (
										<div className="sell_group" key={index}>
											<div className="info_sell">
												<p className="sell-id m-0">
													{t("order")} #{sell.id}
												</p>
											</div>
											<div className="sell_items">
												{sell.sellRecords
													.filter(s => !s.deleted)
													.map(
														(sellRecord, index) => {
															return (
																<SellRecordTwo
																	key={index}
																	sellRecord={
																		sellRecord
																	}
																/>
															);
														}
													)}
											</div>
										</div>
									);
								})}
						</div>
					</div>
					<div className="summary">
						<div className="table-wrapper">
							<div className="table table-summary">
								<div className="summary-subtotal">
									<p>{t("productstwo")}</p>
									<p>{formatterCurrency(total)}</p>
								</div>
								{sellsDiscount > 0 ? (
									<div className="summary-subtotal discount">
										<p>{t("discount")}</p>
										<p>
											{formatterCurrency(sellsDiscount)}
										</p>
									</div>
								) : null}
								{shippingService.price ? (
									<div className="summary-shipping">
										<p>{t("delivery")}</p>
										<p>
											{formatterCurrency(
												shippingService.price[
													shippingService.price.first
												]
											)}
										</p>
									</div>
								) : (
									""
								)}
							</div>
							<div className="location">
								<p>
									{deliveryGroup.shippingAddress.addressLine1}
								</p>
							</div>
							<div className="payment">
								<p>{t("payment")}:</p>
								<p>
									<img
										src={`https://app.ox-sys.com${paidPaymentMethod?.icon}`}
										alt="card"
										width="25"
										height="20"
									/>
									<span>{paidPaymentMethod.name}</span>
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
